'use client';
import { useEffect, useState } from "react";

type Project = {
    id: number;
    name: string;
    hours: number;
};

export const TimeTracker = () => {
    const [isCounting, setIsCounting] = useState(false);
    const [time, setTime] = useState(0);
    const [totalTime, setTotalTime] = useState(0);
    const [projects, setProjects] = useState<Project[]>([]);

    useEffect(() => {
        async function fetchProjects() {
            try {
                const response = await fetch('/api/projects');
                if (!response.ok) {
                    throw new Error('Failed to fetch projects');
                }
                const data = await response.json();
                setProjects(data);
            } catch (error) {
                console.error('Error fetching projects:', error);
            }
        }
        fetchProjects();
    }, []);

    useEffect(() => {
        let timer: NodeJS.Timeout | undefined;
        if (isCounting) {
            timer = setInterval(() => {
                setTime((prevTime) => prevTime + 1);
            }, 1000);
        }
        return () => clearInterval(timer);
    }, [isCounting]);

    const handleStart = () => {
        setIsCounting(true);
    };

    const handleStop = () => {
        setIsCounting(false);
        setTotalTime((prevTotal) => prevTotal + time);
        setTime(0);
    };

    const formatTime = (seconds: number) => {
        const hrs = Math.floor(seconds / 3600);
        const mins = Math.floor((seconds % 3600) / 60);
        const secs = seconds % 60;
        return `${hrs.toString().padStart(2, '0')}:${mins
            .toString()
            .padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
    };

    return (
        <div className="row">
            <ul>
                {projects.map((project) => <li key={project.id}>{project.name}</li>)}
            </ul>
            <div className="col-sm-4 gy-3">
                <div className="card">
                    <div className="card-body">
                        <button type="button" className="btn btn-primary" onClick={handleStart}>
                            Start Timer
                        </button>
                        <p className="card-text mt-3">Time: {formatTime(time)} </p>
                    </div>
                </div>
            </div>
            <div className="col-sm-4 gy-3">
                <div className="card">
                    <div className="card-body">
                        <button type="button" className="btn btn-danger" onClick={handleStop}>
                            Stop Timer
                        </button>
                        <p className="card-text mt-3">Current Session Time: {formatTime(time)} </p>
                    </div>
                </div>
            </div>
            <div className="col-sm-4 gy-3">
                <div className="card">
                    <div className="card-body">
                        <p className="card-text">Total Time Worked: {formatTime(totalTime)} </p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default TimeTracker;
